import React, {Component} from "react"


class Graph extends Component {
    componentDidMount() {
        const columnchart = document.querySelector('#columnchart_material');
        if(columnchart){
            const parent = columnchart.parentElement;
            const rect = parent.getBoundingClientRect()
            columnchart.style.width = `${rect.width}px`
            columnchart.style.height = `${rect.height}px`
        }
        window.google.charts.load('current', {'packages': ['bar']});
        window.google.charts.setOnLoadCallback(drawChart);

        function drawChart() {
            var data = window.google.visualization.arrayToDataTable([
                ['Year', 'Sales', 'Expenses', 'Profit'],
                ['2014', 800, 400, 200],
                ['2015', 1170, 460, 250],
                ['2016', 660, 1120, 300],
                ['2018', 1030, 540, 350],
                ['2019', 1030, 540, 350],
                ['2020', 1030, 540, 350],
                ['2021', 1030, 540, 350],
            ]);

            var options = {
                chart: {
                    title: 'Company Performance',
                    subtitle: 'Sales, Expenses, and Profit: 2014-2017',
                }
            };

            var chart = new window.google.charts.Bar(document.getElementById('columnchart_material'));

            chart.draw(data, window.google.charts.Bar.convertOptions(options));
        }
    }


    render() {
        return (
            <div id="columnchart_material" style={{
                width: '800px',
                height: '500px'
            }}></div>
        )
    }
}


export default Graph
