import React, {useState} from 'react';
import backBtn from '../../assets/icons/Path.svg'
import DetailsMain from './components/DetailsMain'
import DetailsDay from './components/DetailsDay'
import DetailsInstructors from './components/DetailsInstructors'
import Libraries from './components/Libraries'
import DetailsEducators from './components/DetailsEducators'
import {Accordion, Card, Button, Modal} from "react-bootstrap";
import {useHistory} from "react-router-dom";

const Details = () => {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const history = useHistory();
    const goback = () => {
        history.goBack();
    }
    return (
        <div className='details-section'>
            <div className="user-section">
                <div className="user-work">
                    <div className="user-work-back" onClick={goback}>
                        <img src={backBtn} />
                            <p>Back</p>
                    </div>
                    <div className="user-work-btns">
                        <button onClick={handleShow}><span className="material-icons">mail_outline</span>Email</button>
                        <button><span className="material-icons">edit</span>Edit</button>
                    </div>
                </div>
            </div>
            <div className="content details-container">
                <div className="details">
                    <DetailsMain />
                    <DetailsDay />
                </div>
                <div className="users-type">
                    <DetailsInstructors />
                    <Libraries />
                    <DetailsEducators />
                </div>
            </div>

            <Modal size="lg" show={show} onHide={handleClose} className='details-modal'>
                <Modal.Header>
                    <Modal.Title>How to Build a Mobile App Using Data Science</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                   <div className="input-feilds">
                       <div className="input-field">
                           <label htmlFor="subject">Subject</label>
                           <input type="text" className='input' placeholder="Subject"/>
                       </div>
                       <div className="input-field">
                           <label htmlFor="desc">Description</label>
                           <textarea name="desc" className='input' rows="3"></textarea>
                       </div>
                   </div>
                </Modal.Body>
                <Modal.Footer className='modal-footer'>
                    <Button onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button onClick={handleClose}>
                        Send Email
                    </Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default Details

