import React,{useState} from 'react'
import clsx from "clsx";
import file from '../../../../assets/icons/File.svg';


const Libraries = () => {
    const [librariesType, setLibrariesType] = useState(
        'admin',
        'instructor',
        'educators'
    );
    return (
        <div className="library">
            <h1>Document Library</h1>
            <div className="library-btns">
                <button className={clsx({ active: librariesType === 'admin' })}
                        onClick={() => setLibrariesType('admin')}
                >Admin
                </button>
                <button className={clsx({ active: librariesType === 'instructor' })}
                        onClick={() => setLibrariesType('instructor')}
                >
                Instructor</button>
                <button className={clsx({ active: librariesType === 'educators' })}
                        onClick={() => setLibrariesType('educators')}
                >
                Educators</button>
            </div>
            {librariesType === 'admin' && (
                <>
                    <h1>Admin</h1>
                </>
            )}
            {librariesType === 'instructor' && (
                <>
                    <h1>Instructor</h1>
                </>
            )}
            {librariesType === 'educators' && (
                <>

                    <div className="files">
                        <div className="file">
                            <img src={file} alt="" />
                            <p>Artem Sazonov.docx</p>
                        </div>
                    </div>
                </>
            )}
        </div>
    )
}

export default Libraries


