import React from "react";
import {Switch, Route} from 'react-router-dom';
import Dashboard from "../views/Dashboard";
import Workshops from "../views/Workshops";
import Instructors from "../views/Instructors";
import ScritpWorkshops from "../views/ScritpWorkshops";
import Educators from "../views/Educators";
import InstructorProfile from "../views/InstructorProfile";
import CurrentWorkshop from "../views/CurrentWorkshop";
import CreateWorkshop from "../views/CreateWorkshop";
import Details from "../views/Details";
import Attendance from "../views/AttendanceList";


export function Router() {
    return (
        <Switch>
            <Route component={Dashboard} path="/dashboard" />
            <Route component={Workshops} path="/workshops" />
            <Route component={ScritpWorkshops} path="/scritpworkshops" />
            <Route component={Instructors} path="/instructors" />
            <Route component={Educators} path="/educators" />
            <Route component={InstructorProfile} path="/instructorprofile" />
            <Route component={CurrentWorkshop} path="/currentworkshop" />
            <Route component={CreateWorkshop} path="/createworkshop" />
            <Route component={Details} path="/details" />
            <Route component={Attendance} path="/attendance" />
        </Switch>
    )
}
